import React,{useState,useEffect} from 'react';
import axios from 'axios';
import './index.css'


const Players = () => {
const [players, setplayers] = useState([])
const [prev, setprev] = useState(0)
const [next, setnext] = useState(1)
    useEffect(() => {
        axios.get('/players').then((response)=>{
          console.log(response.data)
          setplayers(response.data)
        }
        ).catch((err)=>{
      console.log(err)
        })
        }, [])
       const nextcard=()=>{
           if(next>players.length-1){
            setprev(0)
            setnext(1)   
           }
           else{
           setprev(prev+1)
           setnext(next+1)
           }
       }
    return ( <div>
       {players.slice(prev,next).map((item)=>{
           return <div className="card"><img height="250px"width="100%"src={item.preview}/><div className="details"><h2>Name: {item.name}</h2> <h3>Team: {item.team}</h3><h3>Role: {item.role}</h3><button className="btnNext"onClick={nextcard}>Next</button></div></div>
        
        })
       
       } 
       
        
        </div>);
}
 
export default Players;